// id pid/parentId name children
const tree = (list: any) => {
  const mapped: any = {};
  let item;
  for (let i = 0; i < list.length; i += 1) {
    item = list[i];
    if (!item || !item.id) continue;
    mapped[item.id] = item;
  }

  const result = [];
  for (let ii = 0; ii < list.length; ii += 1) {
    item = list[ii];

    if (!item) continue;
    /* jshint -W041 */
    if (item.pid === undefined && item.parentId === undefined) {
      result.push(item);
      continue;
    }
    const parent = mapped[item.pid] || mapped[item.parentId];
    if (!parent) continue;
    if (!parent.children) parent.children = [];
    parent.children.push(item);
  }
  return result;
};

const DICT_FIXED = (function dict() {
  const fixed = [];
  for (const id in DICT) {
    if ({}.hasOwnProperty.call(DICT, id)) {
      let pid;
      if (id.slice(2, 6) !== '0000') {
        pid = id.slice(4, 6) === '00' ? (`${id.slice(0, 2)}0000`) :
          `${id.slice(0, 4)}00`;
      }
      fixed.push({
        id,
        pid,
        name: DICT[id],
        value: DICT[id],
        label: DICT[id],
      });
    }
  }
  return tree(fixed);
}());

export default DICT_FIXED;
